$white:     #fff;
$gray-100:  #f8f9fa;
$gray-600:  #6c757d;
$gray-900:  #212529;
$black:     #000;
$blue:      #0d6efd;
$indigo:    #6610f2;
$purple:    #6f42c1;
$pink:      #d63384;
$red:       #dc3545;
$orange:    #fd7e14;
$yellow:    #ffc107;
$green:     #198754;
$teal:      #20c997;
$cyan:      #0dcaf0;
$primary:   $blue;
$secondary: $gray-600;
$success:   $green;
$info:      $cyan;
$warning:   $yellow;
$danger:    $red;
$light:     $gray-100;
$dark:      $gray-900;

$theme-colors: (
  "primary":   $primary,
  "secondary": $secondary,
  "success":   $success,
  "info":      $info,
  "warning":   $warning,
  "danger":    $danger,
  "light":     $light,
  "dark":      $dark,
  "blue":      $blue,
  "indigo":    $indigo,
  "purple":    $purple,
  "pink":      $pink,
  "red":       $red,
  "orange":    $orange,
  "yellow":    $yellow,
  "green":     $green,
  "teal":      $teal,
  "cyan":      $cyan,
);

